import React from 'react';
import Layout from '../components/layout';
import SEO from '../components/seo';
import Checkout from '../components/checkout/Checkout';
import { graphql, navigate } from 'gatsby';

export default ({ data, location }) => {
  if (location.state) {
    const selectedPackage = data.allPackages.edges.filter(node => node.node.packageid === location.state.selectedPackageId)[0].node;
    // const selectedPackage = data.allPackages.edges.filter(node => node.node.packageid === 50)[0].node;

    return (
      <>
        <Layout>
          <SEO title='Checkout' />
          <Checkout
            selectedPackage={selectedPackage}
            // packageTvCount={1}
            packageTvCount={location.state.packageTvCount}
          />
        </Layout>
      </>
    )
  } else {
    navigate('/za-doma');
    return `null`;
  }
}

export const query = graphql`
  query CheckoutParent {
    allPackages: allGoogleSheetDevOnlyRow {
      edges {
        node {
          id
          packageid
          speed
          package
          tvcount
          duration
          price
          bonuspoints
          pausescuts
          discounts
          individuallity
          timeshift
          archive
          record
        }
      }
    }
  }
`;